.grid-tile {
  border: solid #5f787b 1px;
  background-color: #2d3636;
  text-align: center;
  font-size: 0.4em;
  cursor: pointer;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  color: white;
}

.obsticle {
  background-color: white;
}

.visiting {
  background-color: #afc7f3;
}

.visited {
  background-color: #5f787b;
  border: #eab354 0.5px solid;
}

.startTile {
  background-color: greenyellow !important;
}

.endTile {
  background-color: red !important;
}

.path {
  background-color: #eab354;
  border: solid 0.5px #5f787b;
}
